    font-family: 'Audiowide';
    src: url('../fonts/Audiowide/Audiowide-Regular.ttf') format('truetype');
}
@font-face {
    font-family: 'mokoto';
    src: url('../fonts/mokoto/ttf/mokoto-mokoto-regular-400.ttf') format('truetype');
}

