* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	&::before {
		box-sizing: border-box;
		margin: 0;
		padding: 0;
	}
	&::after {
		box-sizing: border-box;
		margin: 0;
		padding: 0;
	}
}
html {
	margin: 0;
	padding-top: 5px;
	color: $white;
	display: flex;
	flex-direction: column;
	scroll-behavior: smooth;
	gap: 10px;
	font-family: $font-family_1;
}
body {
	margin: 0;
	padding-top: 5px;
	color: $white;
	display: flex;
	flex-direction: column;
	scroll-behavior: smooth;
	gap: 10px;
	font-family: $font-family_1;
}
a {
	font-family: $font-family_2;
	text-decoration: none;
	color: $white
}
.loader-wrapper {
	margin: 0;
	padding: 0;
	position: fixed;
	top: 0;
	background: black;
	z-index: 9999;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: linear 300ms;
	opacity: 1;
	&.hidden{
		opacity: 0;
	}
}
.loader{
	margin: 0 0 2em;
	height: 100px;
	width: 20%;
	text-align: center;
	padding: 1em;
	margin: 0 auto 1em;
	display: inline-block;
	vertical-align: top;
	svg path,
	svg rect{
  		fill: $color;
}
}

.blur {
	filter: blur(5px);
}
h1 {
	font-family: $font-family_2;
	font-size: inherit;
	line-height: 1ch;
	margin: 0;
	padding: 0;
	margin: 0;
	padding: 0;
	font-weight: 400;
	font-style: normal;
	font-size: clamp(3rem, 8vw, 5rem);
}
h2 {
	font-family: $font-family_2;
	font-size: inherit;
	line-height: 1ch;
	margin: 0;
	padding: 0;
}
h3 {
	font-family: $font-family_2;
	font-size: inherit;
	line-height: 1ch;
	margin: 0;
	padding: 0;
}
h4 {
	font-family: $font-family_2;
	font-size: inherit;
	line-height: 1ch;
	margin: 0;
	padding: 0;
}
h5 {
	font-family: $font-family_2;
	font-size: inherit;
	line-height: 1ch;
	margin: 0;
	padding: 0;
}
h6 {
	font-family: $font-family_2;
	font-size: inherit;
	line-height: 1ch;
	margin: 0;
	padding: 0;
}
p {
	margin: 0;
	padding: 0;
	font-size: clamp(11pt, 2vw, 1.5rem);
}
canvas {
	position: fixed;
	top: 0;
	left: 0;
	z-index: -1;
}
button {
	border: none;
	display: inline-block;
	background: white;
	padding: 15px;
	transition: linear;
	transition-duration: 150ms;
	&.bold {
		background: $color;
		&:hover {
			background: $neon;
		}
	}
	&:hover {
		transform: scale(1.115);
		box-shadow: $glow 0px 0px 20px 5px;
	}
	a {
		color: black;
		text-decoration: none;
	}
}
nav {
	button {
		&:hover {
			transform: scale(1.115);
			box-shadow: none;
		}
	}
	z-index: 99;
	position: fixed;
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	color: $white;
	font-size: 1.5rem;
	font-family: $font-family_2;
	.right {
		span {
			padding: 0;
			position: relative;
		}
		display: flex;
		transform: translateY(-2px);
		text-align: right;
		align-items: center;
		.bars {
			display: none;
		}
	}
	.left {
		text-align: left;
	}
	a {
		color: $white;
		text-decoration: none;
		font-size: clamp(1.2rem, 50%, 1.5rem);
		&:hover {
			cursor: pointer;
			text-shadow: $glow 0px 0px 20px;
			color: $neon;
		}
	}
	img {
		width: clamp(50px, 3vw, 100px);
	}
}
.padded {
	padding-left: 1in;
	padding-right: 1in;
}
.bold {
	background: var($color);
	color: $white;
	&:hover {
		background: var($glow);
	}
	a {
		color: $white;
	}
}
.nitems {
	display: flex;
	gap: 50px;
}
a {
	font-size: clamp(1.2rem, 50%, 1.5rem);
}
.button {
	display: inline-block;
	background: white;
	border-radius: 5px;
	padding: 10px;
	transition: linear;
	transition-duration: 150ms;
	a {
		color: $black;
		text-decoration: none;
	}
	&:hover {
		transform: scale(1.115);
		box-shadow: rgba(0, 0, 0, 0.296) 0px 5px 10px;
	}
}
.wrapper-left {
	display: flex;
	align-self: flex-start;
	width: 50%;
	height: 50%;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	gap: 20px;
	
	iframe{
		width: 100%;
		height: 100%;
	}
}
.wrapper-right {
	display: flex;
	align-self: flex-end;
	width: 50%;
	height: 50%;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 20px;
}
.wrapper-center {
	display: flex;
	align-self: center;
	width: 100%;
	height: 100%;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	gap: 20px;
	.wrapper-left{
		height: 100%;
	}
	.wrapper-right{
		height: 100%;
	}

}
.wrapper {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	.wrapper-left{
		height: 100%;
	}
	.wrapper-right{
		height: 100%;
	}
}
section {
	height: calc(100vh - 20px);
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-shadow: rgba(0, 0, 0, 0.296) 0px 5px 10px;
}
@media (max-width: 768px) {
	nav {
		span {
			padding: 5px;
		}
		.right {
			display: flex;
			flex-direction: column;
			.bars {
				display: flex;
				position: relative;
				flex-direction: column;
				justify-content: space-around;
				align-items: end;
				align-self: flex-end;
				width: 25px;
				height: 25px;
				padding: 0;
				margin: 0;
				background: none;
				&:hover {
					cursor: pointer;
				}
				span {
					padding: 0;
					width: 80%;
					height: 3px;
					border-radius: 5px;
					background: white;
					transition: linear 100ms;
					transform-origin: right;
				}
			}
			.active {
				span {
					&:nth-child(2) {
						width: 0;
					}
					&:nth-child(1) {
						transform: rotate(-55deg);
					}
					&:nth-child(3) {
						transform: rotate(55deg);
					}
				}
			}
		}
	}
	.nitems {
		padding-top: 20px;
		flex-direction: column;
		transform: translate(200px);
		transition: linear 150ms;
	}
	.come {
		transform: translate(0);
	}
	.active {
		background: red;
	}
	.wrapper-left {
		width: 100%;
		align-items: center;
		text-align: center;
	}
}
@media (max-width: 480px) {
	nav {
		font-size: 1rem;
		width: calc(100% - 10px);
	}
	.padded {
		padding: 5px;
	}
	.button {
		padding: 5px;
	}
}
